"use client"

import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { supabase } from "@/lib/supabaseClient"
import { Filter, Search, UserPlus } from "lucide-react"
import { useEffect, useState } from "react"

interface Student {
  id: string
  first_name: string
  last_name: string
  student_number: string
  grade_level: string
  section: string
  email: string
  status: string
}

export default function StudentManagementPage() {
  const [students, setStudents] = useState<Student[]>([])
  const [loading, setLoading] = useState(false)
  const [searchTerm, setSearchTerm] = useState("")
  const [showAddDialog, setShowAddDialog] = useState(false)
  const [newStudent, setNewStudent] = useState({
    first_name: "",
    last_name: "",
    middle_name: "",
    student_number: "",
    grade_level: "",
    section: "",
    email: "",
    phone_number: "",
    date_of_birth: "",
    address: "",
  })
  const [addingStudent, setAddingStudent] = useState(false)
  const [addError, setAddError] = useState("")

  const fetchStudents = async () => {
    setLoading(true)
    try {
      const response = await fetch("/api/admin/students")
      const result = await response.json()
      if (result.success && result.students) {
        setStudents(result.students)
      }
    } catch (error) {
      console.error("Error fetching students:", error)
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    fetchStudents()
  }, [])

  const filteredStudents = students.filter((student) =>
    `${student.first_name} ${student.last_name} ${student.student_number}`
      .toLowerCase()
      .includes(searchTerm.toLowerCase())
  )

  const handleAddStudent = async (e: React.FormEvent) => {
    e.preventDefault()
    setAddingStudent(true)
    setAddError("")

    try {
      // Generate temporary password
      const tempPassword = `Student${Math.random().toString(36).slice(-8)}`

      // Create auth account
      const { data: authData, error: authError } = await supabase.auth.signUp({
        email: newStudent.email,
        password: tempPassword,
        options: {
          data: {
            role: "student",
            first_name: newStudent.first_name,
            last_name: newStudent.last_name,
          },
        },
      })

      if (authError) throw authError

      // Insert into users table
      const { error: insertError } = await supabase.from("users").insert({
        id: authData.user?.id,
        first_name: newStudent.first_name,
        last_name: newStudent.last_name,
        middle_name: newStudent.middle_name || null,
        email: newStudent.email,
        phone_number: newStudent.phone_number || null,
        student_number: newStudent.student_number,
        grade_level: newStudent.grade_level,
        section: newStudent.section || null,
        date_of_birth: newStudent.date_of_birth || null,
        address: newStudent.address || null,
        role: "student",
        status: "Active",
      })

      if (insertError) throw insertError

      // Refresh student list
      await fetchStudents()

      // Reset form and close dialog
      setNewStudent({
        first_name: "",
        last_name: "",
        middle_name: "",
        student_number: "",
        grade_level: "",
        section: "",
        email: "",
        phone_number: "",
        date_of_birth: "",
        address: "",
      })
      setShowAddDialog(false)

      alert(`Student added successfully!\n\nLogin Credentials:\nEmail: ${newStudent.email}\nPassword: ${tempPassword}\n\nPlease save these credentials!`)
    } catch (error: any) {
      console.error("Error adding student:", error)
      setAddError(error?.message || "Failed to add student. Please try again.")
    } finally {
      setAddingStudent(false)
    }
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <div>
          <h2 className="text-3xl font-bold text-red-800">Student Management</h2>
          <p className="text-gray-600">Manage student records and information</p>
        </div>
        <Button className="bg-red-800 hover:bg-red-700">
          <UserPlus className="w-4 h-4 mr-2" />
          Add Student
        </Button>
      </div>

      <Card>
        <CardHeader>
          <div className="flex items-center justify-between">
            <CardTitle className="text-red-800">All Students</CardTitle>
            <div className="flex items-center space-x-2">
              <div className="relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-4 h-4" />
                <Input
                  placeholder="Search students..."
                  className="pl-10 w-64"
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                />
              </div>
              <Button variant="outline" size="icon">
                <Filter className="w-4 h-4" />
              </Button>
            </div>
          </div>
        </CardHeader>
        <CardContent>
          {loading ? (
            <div className="text-center py-8">
              <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-red-800 mx-auto"></div>
              <p className="text-gray-600 mt-2">Loading students...</p>
            </div>
          ) : (
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Student Number</TableHead>
                  <TableHead>Name</TableHead>
                  <TableHead>Grade Level</TableHead>
                  <TableHead>Section</TableHead>
                  <TableHead>Email</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead>Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {filteredStudents.length > 0 ? (
                  filteredStudents.map((student) => (
                    <TableRow key={student.id}>
                      <TableCell className="font-medium">{student.student_number}</TableCell>
                      <TableCell>{`${student.first_name} ${student.last_name}`}</TableCell>
                      <TableCell>{student.grade_level || "N/A"}</TableCell>
                      <TableCell>{student.section || "N/A"}</TableCell>
                      <TableCell>{student.email || "N/A"}</TableCell>
                      <TableCell>
                        <Badge className="bg-green-100 text-green-800">
                          {student.status || "Active"}
                        </Badge>
                      </TableCell>
                      <TableCell>
                        <Button variant="outline" size="sm">
                          View
                        </Button>
                      </TableCell>
                    </TableRow>
                  ))
                ) : (
                  <TableRow>
                    <TableCell colSpan={7} className="text-center text-gray-500">
                      No students found
                    </TableCell>
                  </TableRow>
                )}
              </TableBody>
            </Table>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
